
.header_main {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
    display: block;
}

.header_main_Logo > a > img {
    display: block;
    margin: auto;
}

.header_main_LogoAndInfo {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.header_main_LogoAndInfo > div {
    display: inline;
}

.header_main_ShopMenu > ul {
    margin-right: 10px;
    margin-top: 100px;
    padding-right: 30px;
    list-style: none;
}

.header_main_ShopMenu > ul > li {
    margin-left: 20px;
    display: inline;
}

.mainNav {
    width: 1200px;
}

.MainNav > ul{
    display: block;
    padding: 0;
    margin-left: auto;
    margin-right: auto;
}

.MainNav > ul > li {
    display: inline;
    width: 200px;
    text-align: center;
    list-style: none;
}

.MainNav > ul > li > a {
    width: 200px;
}